'use client'

import React from 'react'
import {
    Tabs,
    TabsContent,
    TabsList,
    TabsTrigger,
} from "@/components/ui/tabs"
import PlanDeatilsPage from './planDetails-page'
import PaymentHistoryPage from './paymentHistory-page'

const BillingPage = () => {
    return (
        <div className="max-w-4xl space-y-6">

            <Tabs defaultValue="plan" className="w-full gap-6">
                <TabsList className="grid grid-cols-4 w-full">
                    <TabsTrigger value="plan">Plan Details</TabsTrigger>
                    <TabsTrigger value="history">Payment History</TabsTrigger>
                </TabsList>

                <TabsContent value="plan" className="flex justify-center">
                   <PlanDeatilsPage/>
                </TabsContent>
                <TabsContent value="history" className="flex justify-center">
                    <PaymentHistoryPage />
                </TabsContent>

            </Tabs>

        </div>
    )
}

export default BillingPage