'use client'

import React from 'react'
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Separator } from "@/components/ui/separator"

const PlanDeatilsPage = () => {
    return (
        <div className='space-y-6'>
            {/* ---------------- Current Plan ---------------- */}
            <Card>
                <CardHeader className="flex flex-row items-center justify-between">
                    <CardTitle>Current Plan</CardTitle>
                    <Badge variant="success">Active</Badge>
                </CardHeader>

                <CardContent className="space-y-3">
                    <div className="flex items-center justify-between">
                        <div>
                            <p className="text-lg font-medium">Pro Plan</p>
                            <p className="text-sm text-muted-foreground">
                                ₹299 / month
                            </p>
                        </div>

                        <Button variant="outline">
                            Change Plan
                        </Button>
                    </div>

                    <Separator />

                    <div className="text-sm text-muted-foreground">
                        Next billing on <strong>25 Sep 2025</strong>
                    </div>
                </CardContent>
            </Card>

            {/* ---------------- Plan Features ---------------- */}
            <Card>
                <CardHeader>
                    <CardTitle>Your Plan Includes</CardTitle>
                </CardHeader>

                <CardContent className="space-y-2 text-sm">
                    <p>✔ Unlimited customers</p>
                    <p>✔ Payment verification</p>
                    <p>✔ Reward management</p>
                    <p>✔ Analytics access</p>
                </CardContent>
            </Card>


            {/* ---------------- Actions ---------------- */}
            <Card>
                <CardHeader>
                    <CardTitle>Manage Subscription</CardTitle>
                </CardHeader>

                <CardContent className="flex gap-3">
                    <Button>Upgrade Plan</Button>
                    <Button variant="outline">Cancel Subscription</Button>
                </CardContent>
            </Card>
        </div>
    )
}

export default PlanDeatilsPage